@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500');

*, ::before, ::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {

  /* colors */
  --body-bg-color: #FFFFFF;
  --sidebar-bg-color: #f4f4f4;
  --text-color: #333333;
  --title-color: #111111;
  --date-color: #1f94c2;
  --dark-body-bg-color: #242424;
  --dark-sidebar-bg-color: #171717;
  --dark-text-color: #d8d8d8;
  --dark-title-color: #ffffff;
  --dark-date-color: #e6ef71;
  
  --icon-color:var(--text-color);
  --circle-color: #AAA;
  --circle-color-plain: #555;  
  --dark-icon-color:var(--dark-text-color);
  --dark-circle-color: rgb(108, 108, 108);
  --dark-circle-color-plain: rgb(238, 238, 238);

  /* bonus */

  /* bae5ff */

  /* Fonts */
  --body-font: "Poppins", "Sans-serif";
  --h1-font-size: 1.7rem;
  --h2-font-size: 1.3rem;
  --h3-font-size: 1.1rem;
  --h4-font-size: 1rem;

  /* Helpers */
  --spacing-1: .4rem;
  --spacing-2: .8rem;
  --spacing-3: 1.2rem;
  --spacing-4: 1.5rem;
  --spacing-5: 2rem;
}

body {
  font-family: var(--body-font);  
  font-weight: 200;
  background-color: var(--bg-color);
  color: var(--text-color);  
}
.dark {  
  background-color: var(--dark-body-bg-color);
  color: var(--dark-text-color);
}

h1, h2, h3 {
  margin-bottom: var(--spacing-1);
  font-weight: 400;    
  color: var(--title-color);
}

.dark h1, .dark h2, .dark h3 {
  color: var(--dark-title-color);
}

h1 {
  font-size:var(--h1-font-size);
  text-transform: uppercase;
 
}

h2 {
  display: flex;
  align-items: center;  
  margin-bottom: var(--spacing-2);
  font-size: var(--h2-font-size);
  letter-spacing: 4px;
  text-transform: uppercase;
  border-bottom : 1px solid rgb(95, 95, 95);
}

h3 {
  font-size: var(--h3-font-size);
}

h4 {
  font-size: var(--h4-font-size);
}

p {
  margin-bottom: var(--spacing-2);
}

ul {
  padding-left:1.3rem;
}

li {
  margin-bottom: var(--spacing-2);
}

a {
  text-decoration: none;
  color: var(--text-color);
}

.dark a {
  color: var(--dark-text-color);
}

button {
  background: none;
  border:none;
  outline: none;
  cursor: pointer;
}

.dark button {
  color: var(--dark-text-color);
}

/* Helpers */
.mb3 {
  margin-bottom: var(--spacing-3);
}
.mb5 {
  margin-bottom: var(--spacing-3);
}
.profil p {
  margin : 0;
 
}

/* Grids */
.grid__row {
  margin: var(--spacing-3) 0;
  
}

.cursus .grid__row {
  padding : 5px 0;
}

@media screen and (min-width:1000px) {
  .grid__container {
    display: grid;  
    grid-template-columns: 1fr 3fr;
  }
  .grid__row {
    display: grid;
    grid-template-columns: 1fr 2fr;
    margin: 0;
  }
}


/* Layout */

.sidebar { 
   /* bonus
  width:100%;
  position: relative;
  */
  padding:1.5rem;
  background-color: var(--sidebar-bg-color); 
}

