 */  
.dark .sidebar {
  background-color: var(--dark-sidebar-bg-color);
}

.actions {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.main {
  padding:1.5rem;  
}

.circle {
  display: inline-block;
  width:10px;
  height: 10px;
  margin-right: 8px;
  border-radius: 50%;
  background:var(--circle-color);
}
.circle--plain {  
  background:var(--circle-color-plain);
}

.dark .circle {
  background: var(--dark-circle-color);
}
.dark .circle--plain {
  background: var(--dark-circle-color-plain);
}

/* Generate PDF */

#cv-print {
  width: 100% !important;
} 
.cv-print ul {
  margin:0;
  padding:0;
  
}
.cv-print ul li {
  list-style-type:none;
}
.cv-print ul li::before {
  content: "•";
  display: inline-block; 
  font-size: 110%;
  padding-right: 10px;
}
.cv-print .grid__container {
  font-size:12px !important;
  max-width: 210mm;
}
.cv-print .sidebar {
  height: 297mm;
}
/*.cv-print .user__infos svg {
  display: none !important;
}*/
.cv-print .circle {
  width:8px;
  height:8px;
}
.cv-print .grid__date {
  margin-top:5px;
}

.cv-print .actions {
  display: none;
}

.cv-print .grid__title {
  font-size: 14px !important;
}
.cv-print .skill__label {
  font-size: 13px;
   
}

@media screen and (max-width:1000px) {
  .btn-pdf {
    display : none;
  }
  
}

